@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  color: #121212;
  font-size: 16px;
  line-height: 24px;
}

img {
  max-width: 100%;
}

.t1 {
  font-family: "Roboto", sans-serif;
  font-size: 42px;
  font-weight: 700;
  line-height: 50px;
}

.btn,
.btn:visited {
  display: inline-block;
  background: #E4C1AE;
  color: #fff;
  font-size: 16px;
  line-height: 16px;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  padding: 16px 40px;
  border-radius: 32px;
  transition: 0.3s all;
  text-decoration: none;
}
.btn:hover, .btn:focus, .btn.btn--active,
.btn:visited:hover,
.btn:visited:focus,
.btn:visited.btn--active {
  background: #d7a488;
}

.hidden {
  display: none;
}

.offer-img__container {
  max-width: 1100px;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
}
.offer-img__img, .offer-img__content {
  flex-basis: 100%;
}
.offer-img__img {
  text-align: center;
}
.offer-img__content {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.offer-img__title {
  padding-bottom: 24px;
}
.offer-img__description {
  padding-bottom: 32px;
}
.offer-img__wrap {
  flex-basis: 100%;
  max-width: 784px;
  padding: 30px 26px;
}
@media (min-width: 1100px) {
  .offer-img {
    padding: 96px 0;
  }
  .offer-img__container {
    padding: 0;
    flex-wrap: nowrap;
  }
  .offer-img__content {
    flex-basis: 41.6666666667%;
    justify-content: center;
    padding: 1rem 0 2rem;
  }
  .offer-img__wrap {
    flex-basis: 380px;
    max-width: 380px;
  }
  .offer-img__img {
    flex-basis: 58.3333333333%;
  }
  .offer-img__img img {
    max-width: 505px;
  }
}

.popup {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  left: 0;
}
.popup__background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #121212;
  opacity: 0.5;
}
.popup__content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: white;
  box-shadow: 0 0 15px #3e3e3e;
}
.popup__title {
  padding-bottom: 24px;
}
.popup__subtitle span {
  font-weight: 700;
}
.popup__close {
  top: 46px;
  right: 46px;
  position: absolute;
  transform: translate(100%);
}
.popup__close:after, .popup__close:before {
  content: "";
  top: 0;
  left: 0;
  width: 15px;
  height: 3px;
  background: #000;
  display: inline-block;
  transform: rotate(45deg);
  position: absolute;
}
.popup__close:before {
  transform: rotate(-45deg);
  right: -15px;
  left: auto;
}
@media (min-width: 1100px) {
  .popup__content {
    padding: 56px 95px;
    width: 920px;
  }
}